@import url(https://fonts.googleapis.com/css?family=Nunito|Shadows+Into+Light);
.card-container {
    height: 230px;
    width: 240px;
    border: 1px solid grey;
    box-shadow: 1px 1px 8px #888888;
    margin: 15px;
}

.img-container {
    height: 60%;
    background-color: rgb(224, 222, 222);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.id-container {
    background-color: rgb(197, 197, 197);
    font-size: 10px;
    display: inline-block;
    padding: 3px 6px;
    position: absolute;
    bottom: 0;
    left: 0;
    color: brown;
}

.info-container {
    height: 40%;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 20px;
}

.poke-name {
    font-size: 20px;
    text-transform: capitalize;
}

.class-info {
    font-size: 10px;
    text-transform: uppercase;
    color: brown;
    display: flex;
}

.class-info__item {
    border: 1px solid grey;
    border-radius: 5px;
    padding: 0 5px;
    margin-top: 5px;
}

.class-info__item:first-child {
    margin-right: 5px;
}
.poke-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 20px 0;
}
.input-label {
    display: none;
}

.input-form {
    display: flex;
    justify-content: center;
}

.input {
    width: 250px;
    height: 50px;
    margin-top: 20px;
    border-radius: 0;
    border: 0;
    padding-left: 10px;
    font-size: 16px;
    font-family: 'Nunito', sans-serif;
    box-shadow: 1px 1px 8px grey;
}

.input::-webkit-input-placeholder {
    text-align: center;
    font-size: 16px;
    font-family: 'Nunito', sans-serif;
}

.input::-ms-input-placeholder {
    text-align: center;
    font-size: 16px;
    font-family: 'Nunito', sans-serif;
}

.input::placeholder {
    text-align: center;
    font-size: 16px;
    font-family: 'Nunito', sans-serif;
}

@media all and (min-width: 768px) {
    .input {
        width: 500px;
        font-size: 20px;
        padding-left: 20px;
    }
    .input::-webkit-input-placeholder {
        font-size: 20px;
    }
    .input::-ms-input-placeholder {
        font-size: 20px;
    }
    .input::placeholder {
        font-size: 20px;
    }
}

@media all and (min-width: 1200px) {
    .input {
        width: 700px;
    }
}
.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loading-text {
    font-family: 'Shadows Into Light', cursive;
    font-size: 32px;
}

.deco1 {
    height: 100px;
    width: 100px;
    background-color: black;
    position: fixed;
    top: 0;
    left: 0;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    z-index: -1;
}

.deco2 {
    height: 100px;
    width: 100px;
    background-color: black;
    position: fixed;
    top: 0;
    right: 0;
    -webkit-transform: translate(50%, -50%) rotate(45deg);
            transform: translate(50%, -50%) rotate(45deg);
    z-index: -1;
}

.mofletes {
    bottom: 0;
    left: 0;
    height: 100px;
    position: fixed;
    z-index: -1;
    width: 100%;
}

.deco3 {
    height: 200px;
    width: 200px;
    background-color: red;
    border-radius: 100px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    position: absolute;
}

.deco4 {
    height: 200px;
    width: 200px;
    background-color: red;
    border-radius: 100px;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    position: absolute;
    right: 0;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    background-color: rgb(248, 248, 34);
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    font-family: 'Nunito', sans-serif;
    position: relative;
}

li {
    list-style: none;
}
